import { Component, OnInit, ViewChild, ViewChildren, QueryList, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FuseConfigService } from '@fuse/services/config.service';
import { RoutesService } from '../routes.service';
import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { locale as spanish } from '../i18n/es';
import htmlToImage from 'html-to-image';
import { NetworkService } from 'app/services/network.service';
import { FuseProgressBarService } from '@fuse/components/progress-bar/progress-bar.service';
import { TranslateService } from '@ngx-translate/core';
import { MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition, MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit, OnDestroy {

  @ViewChildren('container')
  public divs: QueryList<any>;
  @ViewChild('container', { static: true }) container;
  @ViewChild('mydiv', { static: true }) div;

  @ViewChild('mycanvas', { static: true }) canvas;
  route;
  elements = [];
  elementRefs = [];
  levels = [];
  loading = true;
  drawed = false;

  events: any;
  readonly size = 50;
  levelW = 0;
  ctx;
  w;
  h;

  xPos: MatSnackBarHorizontalPosition = 'center';
  yPos: MatSnackBarVerticalPosition = 'top';

  constructor(
      private _fuseTranslationLoaderService: FuseTranslationLoaderService,
      private fuseProgressBarService: FuseProgressBarService,
      private router: Router,
      private fuseConfigService: FuseConfigService,
      private activatedRoute: ActivatedRoute,
      private service: RoutesService,
      private networkService: NetworkService,
      private changesDetector: ChangeDetectorRef,
      private translateService: TranslateService,
      private snack: MatSnackBar,
    ) {
    this.fuseConfigService.config = {
      layout: {
        navbar: {
          hidden: false
        },
        toolbar: {
          hidden: false
        }
      }
    }

    this._fuseTranslationLoaderService.loadTranslations(spanish);
  }

  ngOnInit() {
    this.fuseProgressBarService.show();
    this.ctx = this.canvas.nativeElement.getContext("2d");
    
    this.w = 0
    this.h = 0

    this.activatedRoute.paramMap.subscribe((res: any) => {
      this.getRoute(res.params.routeId);
    })
  }

  ngAfterViewInit() {

    this.events = this.divs.changes.subscribe(val => {
      this.elementRefs = [];

      this.w = this.container.nativeElement.offsetWidth;
      this.h = this.container.nativeElement.offsetHeight;

      let interval = setInterval(() => {
        if(!this.loading) {
          clearInterval(interval);

          for(let el of val.first.nativeElement.children) {
            if(el.id) {
              this.elementRefs.push({ div_id: el.id , el_id: parseInt(el.id.substring(el.id.indexOf('_') + 1)) });
            }
          }
          this.draw();
        } else console.log('Loading...')
      }, 100);
    });
  }

  ngAfterViewChecked() {
    if(this.drawed) {
      this.w = this.container.nativeElement.offsetWidth;
      this.h = this.container.nativeElement.offsetHeight;
      this.draw();
    }
    this.changesDetector.detectChanges();
    
  }

  ngDoCheck() {
    
  }

  getRoute(id) {
    this.route = [];
    this.service.getRoute(id).subscribe(res => {
      this.route = res;
      this.service.routeName = res.name;
      this.getElementList();
    })
  }

  loadedImages = 0;
  totalImages;

  getElementList() {
    this.elements = [];
    this.service.getElementList().subscribe(res => {
      this.elements = res;
      this.snack.open(this.translateService.instant('ROUTE.LOADING_IMAGES'), 'INFO', {
        horizontalPosition: this.xPos,
        verticalPosition: this.yPos,
        duration: 99999,
        panelClass: 'blue',
      });

      this.totalImages = this.route.tree.length;
      for(let el of this.elements) {
        if(!this.route.tree.find(element => element.element_id == el.id)) continue;
        el.image_url = null;
        this.service.getElementImage(el.id).subscribe(res => {
          var reader = new FileReader();
          reader.readAsDataURL(res);
          reader.onload = (event) => {
            el.image_url = reader.result;
            this.loadedImages++;
            if(this.loadedImages == this.totalImages) this.snack.dismiss();
          }
        });
      }
      this.loading = false;
    });
  }

  draw() {
    this.drawed = true;
    this.ctx.clearRect(0, 0, 2000, 2000);
    this.ctx.beginPath();
    let root = -1;
    
    for(let route of this.route.tree) {
      
      if (route.parent_element_id == 0) {
        root = route.element_id;
        continue;
      }
      let start = this.getPoints(this.elementRefs.find(el => el.el_id == route.element_id));
      let end = this.getPoints(this.elementRefs.find(el => el.el_id == route.parent_element_id));
      
      let angle = this.getAngle(end[1] + this.size, end[0] + this.size, start[1] + this.size, start[0] + this.size)
      let points = this.getCoordinates(start[0], start[1], end[0], end[1], angle, route.parent_element_id == root);
      let arrowPoints = this.getArrowHead(points[6], points[7], points[8]);

      this.ctx.beginPath();
      this.ctx.lineWidth = 2;
      this.ctx.fillStyle = '#6a6a6a';
      this.ctx.strokeStyle = '#6a6a6a';

      this.ctx.moveTo(points[0], points[1]);
      this.ctx.lineTo(points[2], points[3]);
      this.ctx.lineTo(points[4], points[5]);
      this.ctx.lineTo(points[6], points[7]);

      this.ctx.stroke();

      this.ctx.beginPath();

      this.ctx.lineWidth = 2.5;
      this.ctx.moveTo(points[6], points[7]);
      this.ctx.lineTo(arrowPoints[0], arrowPoints[1]);
      this.ctx.moveTo(points[6], points[7]);
      this.ctx.lineTo(arrowPoints[2], arrowPoints[3]);

      this.ctx.stroke();

      this.fuseProgressBarService.hide();
    }
  }

  getPoints(element) {
    return [parseInt(this.divs.first.nativeElement.children[element.div_id].style.left.replace('px', '')),
      parseInt(this.divs.first.nativeElement.children[element.div_id].style.top.replace('px', ''))];
  }

  getArrowHead(x1, y1, direction) {
    switch(direction) {
      case 0: return this.getArrowPoints(x1, y1, 0);
      case 1: return this.getArrowPoints(x1, y1, 270);
      case 2: return this.getArrowPoints(x1, y1, 180);
      case 3: return this.getArrowPoints(x1, y1, 90);
      default: return null;
    }
  }

  getCoordinates(x1, y1, x2, y2, angle, root) {
    if(root) {
      if(Math.abs(x2 - x1) < this.size * 2.5 || Math.abs(y2 - y1) < this.size * 2.5) {
        if(angle > 315 || angle < 45) {
          return [
            x1 + this.size,
            y1 + this.size,

            (x1 + x2 + this.size * 2) / 2,
            y1 + this.size,
            
            (x1 + x2 + this.size * 2) / 2,
            y2 + this.size * 2,
            
            x2,
            y2 + this.size * 2,
            
            0,
            true
          ];
        } else if(angle < 135) {
          return [
            x1 + this.size,
            y1 + this.size,

            x1 + this.size,
            (y1 + y2 + this.size * 2) / 2,

            x2 + this.size * 2,
            (y1 + y2 + this.size * 2) / 2,

            x2 + this.size * 2,
            y2 + 2 * this.size * 2,

            3,
            true
          ];
        } else if(angle < 225) {
          return [
            x1 + this.size,
            y1 + this.size,

            (x1 + x2 + this.size * 2) / 2,
            y1 + this.size,

            (x1 + x2 + this.size * 2) / 2,
            y2 + this.size * 2,

            x2 + this.size * 2 * 2,
            y2 + this.size * 2,

            2,
            true
          ];
        } else {
          return [
            x1 + this.size,
            y1 + this.size,

            x1 + this.size,
            (y1 + y2 + this.size * 2) / 2,
            
            x2 + this.size * 2,
            (y1 + y2 + this.size * 2) / 2,
            
            x2 + this.size * 2,
            y2,
            
            1,
            true
          ];
        }
      }
      else {
        if(angle < 90 || angle > 270) {
          return [
            x1 + this.size,
            y1 + this.size,
            x1 + this.size,
            y2 + this.size * 2,
            x1 + this.size,
            y2 + this.size * 2,
            x2,
            y2 + this.size * 2,
            0,
            false
          ];
        } else {
          return [
            x1 + this.size,
            y1 + this.size,
            x1 + this.size,
            y2 + this.size * 2,
            x1 + this.size,
            y2 + this.size * 2,
            x2 + this.size * 2 * 2,
            y2 + this.size * 2,
            2,
            false
          ];
        }
      }
    } else {
      if(Math.abs(x2 - x1) < this.size * 2.5 || Math.abs(y2 - y1) < this.size * 2.5) {
        if(angle > 315 || angle < 45) {
          return [x1 + this.size, y1 + this.size, (x1 + x2 + this.size * 2) / 2, y1 + this.size, (x1 + x2 + this.size * 2) / 2, y2 + this.size, x2, y2 + this.size, 0, true];
        } else if(angle < 135) {
          return [x1 + this.size, y1 + this.size, x1 + this.size, (y1 + y2 + this.size * 2) / 2, x2 + this.size, (y1 + y2 + this.size * 2) / 2, x2 + this.size, y2 + 2 * this.size, 3, true];
        } else if(angle < 225) {
          return [x1 + this.size, y1 + this.size, (x1 + x2 + this.size * 2) / 2, y1 + this.size, (x1 + x2 + this.size * 2) / 2, y2 + this.size, x2 + this.size * 2, y2 + this.size, 2, true];
        } else {
          return [x1 + this.size, y1 + this.size, x1 + this.size, (y1 + y2 + this.size * 2) / 2, x2 + this.size, (y1 + y2 + this.size * 2) / 2, x2 + this.size, y2, 1, true];
        }
      }
      else {
        if(angle < 90 || angle > 270) {
          return [x1 + this.size, y1 + this.size, x1 + this.size, y2 + this.size, x1 + this.size, y2 + this.size, x2, y2 + this.size, 0, false];
        } else {
          return [x1 + this.size, y1 + this.size, x1 + this.size, y2 + this.size, x1 + this.size, y2 + this.size, x2 + this.size * 2, y2 + this.size, 2, false];
        }
      }
    }
  }

  getArrowPoints(x1, y1, angle) {
    const size = 13;
    let radians0 = angle * (Math.PI / 180);
    let radians1 = (angle * (Math.PI / 180)) + Math.PI +  Math.PI / 6;
    let radians2 = (angle * (Math.PI / 180)) + Math.PI -  Math.PI / 6;
    let offset = (angle > 0 && angle < 90) || angle > 270 ? -2 * Math.asin(Math.sin(radians0)) : 2 * Math.asin(Math.sin(radians0));

    return [ x1 + (size * Math.cos(radians1 + offset)), y1 + (size * Math.sin(radians1 + offset)),
      x1 + (size * Math.cos(radians2 + offset)), y1 + (size * Math.sin(radians2 + offset))]
  }

  getAngle(currX, currY, endX, endY) {
    var angle = Math.atan2(currX - endX, currY - endY) * (180 / Math.PI);
  
    if (angle < 0) {
      angle = Math.abs(angle);
    } else {
      angle = 360 - angle;
    }
  
    return angle;
  };

  getDivPosition(element) {
    if(element.parent_element_id == 0) {
      return {
        'position': 'absolute',
        'top': element.y * (this.h - 160) / 100 + 10 + 'px',
        'left': element.x * (this.w - 170) / 100 + 20 + 'px',
        'height': '200px',
        'max-height': '200px',
        'min-height': '200px',
        'width': '200px',
        'max-width': '200px',
        'min-width': '200px'
      };
    } else {
      return {
        'position': 'absolute',
        'top': element.y * (this.h - 160) / 100 + 10 + 'px',
        'left': element.x * (this.w - 170) / 100 + 20 + 'px',
      };
    }
  }

  getHeight(element) {
    if(element.parent_element_id == 0) {
      return {
        'max-height': '200px',
        'min-height': '200px',
        'height': '200px'
      }
    } else {
      return {
        'max-height': '100px',
        'min-height': '100px',
        'height': '100px'
      }
    }
  }

  getElementImage(id) {
    if(this.elements.length === 0) {
      return '';
    }
    return this.elements.find(el => el.id == id).image_url;
  }

  getElementName(id) {
    if(this.elements.length === 0) {
      return '';
    }
    return this.elements.find(el => el.id == id).name;
  }

  export() {
    this.snack.open(this.translateService.instant('ROUTE.EXPORTING'), 'INFO', {
      horizontalPosition: this.xPos,
      verticalPosition: this.yPos,
      duration: 99999,
      panelClass: 'blue',
    });

    htmlToImage.toPng(this.container.nativeElement, { backgroundColor: '#eee' }).then(dataUrl => {
      this.div.nativeElement.style.display = 'block';
      var img = new Image();
      img.src = dataUrl;

      img.onload = (e) => {
        var logo = new Image();
        logo.src = 'assets/images/Acreditta_LogoHorizontal.png';
        let c = document.createElement('canvas');

        logo.onload = (e) => {
          let w = img.width;
          let h = img.height;
          let ratio = logo.width/logo.height;
          let wLogo = w * 0.15;
          let hLogo = wLogo/ratio;

          c.height = h;
          c.width = w + wLogo + 40;
          let ctx = c.getContext('2d');
          ctx.fillStyle = '#eee';

          ctx.fillRect(0, 0, w + wLogo + 40, h);
          ctx.drawImage(img, 0, 0);
          ctx.drawImage(logo, w + 20, h - hLogo - 20, wLogo, hLogo);

          var a = document.createElement('a');
          a.href = c.toDataURL("image/png");
          a.download = this.route.name + "-route.png";
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          
          this.snack.open(this.translateService.instant('ROUTE.EXPORTED'), 'INFO', {
            horizontalPosition: this.xPos,
            verticalPosition: this.yPos,
            duration: 5000,
            panelClass: 'green',
          });
        }
      }
    })
  }

  ngOnDestroy() {
    this.service.routeName = null;
    this.snack.dismiss();
  }

}