import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { RoutesService } from '../routes.service';

@Component({
  selector: 'route',
  templateUrl: './route.component.html',
  styleUrls: ['./route.component.scss']
})
export class RouteComponent implements OnInit {
  
  @Input('route') route;
  url;
  img;
  date;

  @Output() view: EventEmitter<any> = new EventEmitter();
  @Output() edit: EventEmitter<any> = new EventEmitter();
  @Output() delete: EventEmitter<any> = new EventEmitter();

  defaultImg = 'assets/images/placeholder.png'

  constructor(private service: RoutesService) { }

  ngOnInit() {
    if(this.route.image_url) {
      if(this.route.image_url.includes('http')) {
        this.url = this.route.image_url;
        this.img = true;
      } else {
        this.service.getRouteImage(this.route.id).subscribe(res => {
          var reader = new FileReader();
          reader.readAsDataURL(res);
          reader.onload = (event) => {
            this.url = reader.result;
            this.img = true;
          }
        });
      }
    } else {
      this.url = this.defaultImg;
      this.img = false;
    }
  }

  getEllipsis(str: string) {
    if(str.length < 120) return str; return str.substring(0, 120).trim() + '...';
  }

  getTimestamp() {
    if(!this.date) this.date = Date.now();
    return this.date;
  }

}