import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { DragDropModule } from '@angular/cdk/drag-drop';

import { FuseSharedModule } from '@fuse/shared.module';

import { MatButtonModule } from '@angular/material/button';
import { MatDialogModule } from '@angular/material/dialog';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatCheckboxModule } from '@angular/material/checkbox';

import { RoutesComponent, DeleteDialogComponent } from './routes.component';
import { CreateComponent, CancelDialogComponent, ResetDialogComponent, RootDialogComponent } from './create/create.component';
import { CreateRouteDialogComponent } from './create/create-route-dialog/create-route-dialog.component';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { RouteComponent } from './route/route.component';
import { DetailComponent } from './detail/detail.component';
import { MatChipsModule } from '@angular/material/chips';
import { MatDividerModule } from '@angular/material/divider';

const routes = [
    {
        path: 'consult',
        component: RoutesComponent
    },
    {
        path: 'create',
        component: CreateComponent
    }, {
        path: 'consult/:routeId',
        component: DetailComponent
    }, {
        path: 'edit/:routeId',
        component: CreateComponent
    }
];

@NgModule({
    declarations: [
        RoutesComponent,
        RouteComponent,
        CreateComponent,
        CreateRouteDialogComponent,
        DeleteDialogComponent,
        CancelDialogComponent,
        ResetDialogComponent,
        RootDialogComponent,
        DetailComponent
    ],
    imports     : [
        RouterModule.forChild(routes),

        TranslateModule,

        FuseSharedModule,

        MatInputModule,
        MatSelectModule,
        MatButtonModule,
        MatTooltipModule,
        MatIconModule,
        MatSidenavModule,
        MatSnackBarModule,
        MatDialogModule,
        MatTooltipModule,
        DragDropModule,
        MatCheckboxModule,
        MatDatepickerModule,
        MatChipsModule,
        MatDividerModule
    ],
    exports     : [
        RoutesComponent,
        CreateComponent,
        DetailComponent
    ],
    entryComponents: [CreateRouteDialogComponent, RootDialogComponent, CancelDialogComponent, DeleteDialogComponent, ResetDialogComponent]
})

export class RoutesModule
{
}