import { Component, OnInit, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { FuseConfigService } from '@fuse/services/config.service';
import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';

import { locale as spanish } from './i18n/es';
import { RoutesService } from './routes.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FuseProgressBarService } from '@fuse/components/progress-bar/progress-bar.service';
import { MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition, MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'routes',
  templateUrl: './routes.component.html',
  styleUrls: ['./routes.component.scss']
})
export class RoutesComponent implements OnInit {

  loading: boolean = true;
  routes = [];

  public horizontalPos: MatSnackBarHorizontalPosition = 'center';
  public verticalPos: MatSnackBarVerticalPosition = 'top';

  constructor(
      private _fuseTranslationLoaderService: FuseTranslationLoaderService,
      private router: Router,
      private fuseConfigService: FuseConfigService,
      private routeService: RoutesService,
      private dialog: MatDialog,
      private fuseProgressBarService: FuseProgressBarService,
      private snackbar: MatSnackBar
    ) {
    this.fuseConfigService.config = {
      layout: {
        navbar: {
          hidden: false
        },
        toolbar: {
          hidden: false
        }
      }
    }

    this._fuseTranslationLoaderService.loadTranslations(spanish);
  }

  ngOnInit() {
    this.fuseProgressBarService.show();
    this.getRoutes();
  }

  getRoutes() {
    this.routeService.getRoutesList().subscribe(res => {
      this.routes = res;
      this.loading = false;
      this.fuseProgressBarService.hide();
    })
  }

  deleteRoute(id) {
    const dialog = this.dialog.open(DeleteDialogComponent, {
      data: {
      }
    });

    dialog.afterClosed().subscribe(res => {
      if(typeof res !== 'undefined') {
        this.fuseProgressBarService.show();
        this.routeService.deleteRoute(id).subscribe(res => {
          if(res.status == 500) {
            this.snackbar.open(res.message, 'ERROR', {
                horizontalPosition: this.horizontalPos,
                verticalPosition: this.verticalPos,
                duration: 5000,
                panelClass: 'red'
            });
        } else {
            this.snackbar.open(res.message, 'INFO', {
                horizontalPosition: this.horizontalPos,
                verticalPosition: this.verticalPos,
                duration: 5000,
                panelClass: 'green'
            });
        }
          this.getRoutes();
        }, err => {
          this.snackbar.open(err.error.message, 'ERROR', {
            horizontalPosition: this.horizontalPos,
            verticalPosition: this.verticalPos,
            duration: 5000,
            panelClass: 'red'
          });
          this.fuseProgressBarService.hide();
        });
      }
    });
  }

  viewRoute(id) {
    this.router.navigate(['routes/consult/' + id]);
  }

  editRoute(id) {
    this.router.navigate(['routes/edit/' + id]);
  }

}

@Component({
  selector: 'delete-dialog',
  templateUrl: './delete-dialog/delete-dialog.component.html',
})
export class DeleteDialogComponent implements OnInit {

  constructor(
      public dialogRef: MatDialogRef<any>,
      private router: Router,
      @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  ngOnInit(): void {

  }

  cancel(): void {
      this.dialogRef.close();
  }

  delete(): void {
      
  }
}