import { OnInit, Component, Input, Inject, ViewEncapsulation, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { Router } from "@angular/router";
import { MatSnackBar, MatSnackBarVerticalPosition, MatSnackBarHorizontalPosition } from "@angular/material/snack-bar";
import { TranslateService } from "@ngx-translate/core";
import { RoutesService } from "../../routes.service";
import { MatChipInputEvent } from "@angular/material/chips";
import { COMMA, ENTER, SEMICOLON } from '@angular/cdk/keycodes';

@Component({
  selector: 'create-route-dialog',
  templateUrl: './create-route-dialog.component.html',
  styleUrls: ['./create-route-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class CreateRouteDialogComponent implements OnInit {
  
  @Input('route') route;
  @ViewChild('file', { static: false }) file: any;
  
  form: FormGroup;
  defaultImg = 'assets/images/placeholder.png';
  image;
  imagePath;
  error: boolean;
  
  public xpos: MatSnackBarHorizontalPosition = 'center';
  public ypos: MatSnackBarVerticalPosition = 'top';
  
  readonly separatorKeysCodes: number[] = [ENTER, COMMA, SEMICOLON];
  elementList = [];
  difficulties = [];
  materiality = [];
  languages = [{ value: 'es', name: null }, { value: 'en', name: null }, { value: 'pt', name: null },
      { value: 'fr', name: null }, { value: 'other', name: null }]

    skills = [];
    tags = [];
  
  constructor(
    public dialogRef: MatDialogRef<any>,
    private router: Router,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private snackbar: MatSnackBar,
    private translate: TranslateService,
    private formBuilder: FormBuilder,
    private routesService: RoutesService
  ) {}
    
  ngOnInit() {
    this.languages.forEach(lang => lang.name = this.translate.instant('LANGUAGES.'.concat(lang.value)));
    this.getData();
  }

  add(event: MatChipInputEvent, name: string): void {
    const input = event.input;
    const value = event.value;
    
    if ((value || '').trim()) {
      switch(name) {
        case 'tags':
          this.tags.push(value.trim());
          break;
  
        case 'skills':
          this.skills.push(value.trim());
          break;
      }
    }

    if (input) {
      input.value = '';
    }
}

remove(skill, name: string): void {
  let index;

  switch(name) {
    case 'tags':
      index = this.tags.indexOf(skill);

      if (index >= 0) {
        this.tags.splice(index, 1);
      }
      break;

    case 'skills':
      index = this.skills.indexOf(skill);

      if (index >= 0) {
        this.skills.splice(index, 1);
      }
      break;
  }
    
}
  
  getData() {
    this.elementList = this.data.elements.reverse();
    this.difficulties = this.data.difficulties;
    this.materiality = this.data.materiality;
    if(this.data.route) {
      const r = this.data.route;
      this.form = this.formBuilder.group({
        name: [r.name, [Validators.required, Validators.minLength(5)]],
        description: [r.description, Validators.maxLength(140)],
        difficulty: [r.difficulty, Validators.required],
        score: [r.score, [Validators.pattern('^[0-9]+$'), Validators.required]],
        skills: [''],
        tags: [''],
        criterion: [r.criterion],
        materiality: [r.materiality, Validators.required],
        expire_at: [r.expire_at],      
        language: [r.language, Validators.required],
      })
      this.skills = r.skills.trim().length > 0 ? r.skills.split(';') : [];
      this.tags = r.tags.trim().length > 0 ? r.tags.split(';') : [];
    } else {
      this.form = this.formBuilder.group({
        name: ['', [Validators.required, Validators.minLength(5)]],
        description: ['', Validators.maxLength(140)],
        difficulty: ['', Validators.required],
        score: [0, [Validators.pattern('^[0-9]+$'), Validators.required]],
        skills: [''],
        tags: [''],
        criterion: [''],
        materiality: ['', Validators.required],
        expire_at: [null],      
        language: ['', Validators.required],
      })
    }

    if(this.data.image) {
      this.routesService.getRouteImage(this.data.route.id).subscribe(res => {
        var reader = new FileReader();
        reader.readAsDataURL(res);
        reader.onload = (event) => {
          this.image = reader.result;
        }
      })
    }
  }
  
  preview(files) {
    if (files.length !== 1) return;
    
    if (files[0].type !== 'image/png') {
      this.snackbar.open(this.translate.instant('ERROR.IMAGE_TYPE_ERROR'), 'ERROR', {
        horizontalPosition: this.xpos,
        verticalPosition: this.ypos,
        duration: 5000,
        panelClass: 'red',
      });
      return;
    }
    
    if (files[0].size > 2097152) {
      this.snackbar.open(this.translate.instant('ERROR.MAX_FILE_SIZE'), 'ERROR', {
        horizontalPosition: this.xpos,
        verticalPosition: this.ypos,
        duration: 5000,
        panelClass: 'red',
      });
      return;
    }
    
    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.image = reader.result;
    }
  }
  
  removeImage() {
    this.file.nativeElement.value = '';
    this.image = null;
  }
  
  save() {
    let file: any = null;
    if(this.image && this.file.nativeElement.files[0]) {
      file = new FormData();
      file.append('archivo', this.file.nativeElement.files[0]);
    }

    this.form.patchValue({
      skills: this.skills.join(';'),
      tags: this.tags.join(';')
    })
    this.dialogRef.close({
      formdata: file,
      form: this.form.value
    });
  }
}
